import * as React from 'react'
import Link from 'gatsby-link'
import Typography from 'material-ui/Typography/Typography';
import WhatshotIcon from 'material-ui-icons/Whatshot';
import AppearingTypography from '../components/Animations/AppearingTypography';
import Grid from 'material-ui/Grid/Grid';
import withStyles from 'material-ui/styles/withStyles';
import { GridSpacing } from 'material-ui/Grid';
import Paper from 'material-ui/Paper/Paper';

const IndexPage = ({ classes }: any) => (
  <div style={{ paddingTop: "4rem" }}>

    <Grid container className={classes.root} justify="center" spacing={16}>
      {[0, 1, 2].map(value => (
        <Grid key={value} item md={4} xs={12}>
          <Paper className={classes.paper}>
            THIS IS A TEST
          </Paper>
        </Grid>
      ))}
    </Grid>
    <AppearingTypography delay={300} variant="headline" color="inherit" style={{ textAlign: "center" }}>
      <WhatshotIcon />
      More details coming soon
    </AppearingTypography>
  </div>
)

export default withStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 140,
    width: "100%",
  },
  control: {
    padding: theme.spacing.unit * 2,
  },
}))(IndexPage)



